// === SWIPER === //

const swiper = new Swiper(".mySwiper", {
  spaceBetween: 30,
  centeredSlides: true,
  loop: true,
  slidesPerView: 4,
  speed: 4000,
  autoplay: {
    // enabled: true,
    delay: 1,
    // disableOnInteraction: false,
    breakpoints: {
      // when window width is <= 320px
      320: {
        slidesPerView: 2,
        spaceBetween: 10,
      },
      // when window width is <= 480px
      480: {
        slidesPerView: 3,
        spaceBetween: 20,
      },
      // when window width is <= 640px
      640: {
        slidesPerView: 4,
        spaceBetween: 25,
      },
    },
  },
});

// === RERENDER HEADER LINKS === //

// Get all nav links and add a scroll event listener to the window.
const navLinks = document.querySelectorAll(".navbar-nav a");
window.addEventListener("scroll", () => {
  // Initialize current section to an empty string.
  let current = "";

  // Get the current scroll position.
  const scrollPosition = document.documentElement.scrollTop + 150;

  // Loop through all nav links.
  navLinks.forEach((item) => {
    // Find the section associated with the link.
    const section = document.querySelector(item.hash);

    // If the section is currently visible, set it as the current section.
    if (
      section.offsetTop <= scrollPosition &&
      section.offsetTop + section.offsetHeight > scrollPosition
    ) {
      current = item.hash;
    }
  });

  // Loop through all nav links again.
  navLinks.forEach((item) => {
    // Remove the active class from all links.
    item.classList.remove("active");

    // If the link is associated with the current section, add the active class.
    if (item.hash === current) {
      item.classList.add("active");
    }
  });
});

